.addItem-container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 1rem;
  /* border: 1px solid #fff; */
}
.addItem-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.addItem-center button {
  margin: 0 0.5rem;
  width: 130px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9rem;
  background-color: #fff;
  color: #000;
  border-radius: 0.25rem;
}
