nav {
  padding: 0.125rem;
  display: flex;
}
.nav-container {
  flex: 1;
  display: flex;
  justify-content: space-between;
  height: 100%;
  /* border: 1px solid red; */
  border: 1px solid #fff;
}
.nav-center {
  flex: 0 1 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
}
.nav-center > h2 {
  color: #fff;
}
.nav-numberCenter {
  flex: 0 1 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: yellow;
  padding: 0.5rem;
  /* border: 1px solid black; */
}

.nav-numberImg {
  color: black;
  font-size: 1.2rem;
}
.nav-numberTable {
  color: black;
  font-size: 1.2rem;
}
