.modal {
  display: none;
}

.show-modal {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
}
.modal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 70%;
  border: 1px solid red;
  background-color: #fff;
  padding: 0.25rem;
  display: flex;
  flex-direction: column;
  /* overflow: hidden; */
}
.modal-container h2 {
  flex: 0 0 50px;
  border: 1px solid red;
}
.modal-center {
  flex: 1;
  overflow: hidden;
}
.modal-item {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 0.7fr 1fr;
  border: 1px solid red;
}
.modal-center img {
  width: 50px;
}
.modal-info {
  /* display: flex; */
  overflow: auto;
}
.btn-row {
  display: flex;
}
.order-btn {
  flex: 0 0 40px;
  display: flex;

  border: 1px solid red;
}
.modal-btn-left {
  flex: 0 0 30%;
  margin-right: 0.25rem;
}
.modal-btn-right {
  flex: 1;
  margin-left: 0.25rem;
}
